   \t:axisFormat=\"axisFormat\"\n        \t:timeFormat=\"timeFormat\"\n        \t:maxTime=\"maxTimes\"\n            :resourceGroupField=\"resourceGroupField\"\n            :resources=\"resources\"\n        \t:minTime=\"minTimes\"/>\n            <!-- 在庫 -->\n            <stock-modal v-if=\"mode === 'stock'\"\n                         :dialogStatus=\"dialogStatus\"\n                         :toggleModal=\"toggleModal\"\n                         :init=\"init\"/>\n            <!-- 詳細 -->\n            <detail-modal v-if=\"mode === 'detail'\"\n                          :dialogStatus=\"dialogStatus\"\n                          :toggleModal=\"toggleModal\"\n                          :detail=\"detail\"/>\n    </el-container>\n</template>\n\n<script>\nimport FullCalendar from '@fullcalendar/vue'\nimport dayGridPlugin from '@fullcalendar/daygrid'\nimport momentPlugin from '@fullcalendar/moment'\nimport timeGridPlugin from '@fullcalendar/timegrid'\nimport resourceTimeline from '@fullcalendar/resource-timeline'\nimport interactionPlugin from '@fullcalendar/interaction'\nimport jaLocale from '@fullcalendar/core/locales/ja'\nimport StockModal from '@/components/Calender/Modal/stock.vue'\nimport DetailModal from '@/components/Calender/Modal/detail.vue'\nimport moment from 'moment/moment'\n\nexport default {\n    components: {\n        FullCalendar,\n        StockModal,\n        DetailModal\n    },\n    props: {\n        event: {\n            type: Array,\n            required: false\n        }\n    },\n    data() {\n        return {\n            calendarPlugins: [\n                dayGridPlugin,\n                momentPlugin,\n                timeGridPlugin,\n                interactionPlugin,\n                resourceTimeline\n            ],\n            locale: jaLocale,\n            events: [],\n            header: {\n                left: 'today prev,next',\n                center: 'title',\n                right: 'dayGridMonth,timeGridWeek,timeGridDay,resourceTimeline'\n            },\n            slotDuration: '00:30:00',\n            snapDuration: '00:15:00',\n            slotLabelFormat: 'H:mm',\n            defaultTimedEventDuration: '10:00:00',\n            axisFormat: 'H:mm',\n            timeFormat: 'H:mm',\n            minTimes: '07:00',\n            maxTimes: '19:00',\n            buttonText: {\n                prev: '',\n                next: '',\n                prevYear: '',\n                nextYear: ''\n            },\n            resourceGroupField: 'building',\n            resources: [\n              { id: 'a', building: '難波ホテル', title: '3人部屋' },\n              { id: 'b', building: '無料ホテル', title: '4人部屋' },\n            ],\n            dialogStatus: false,\n            mode: '',\n            detail: {}\n        }\n    },\n    methods: {\n        toggleModal () {\n          this.dialogStatus = !this.dialogStatus\n        },\n        init () {\n          this.axios.get('/api/v1/bookings')\n          .then(res => {\n            this.bookings = res.data.bookings\n            this.bookings.forEach(value => {\n                if (value.is_blocked) {\n\n                   this.events.push({\n                       id: value.id,\n                       start: moment(value.checkin).format(\"YYYY-MM-DD\"),\n                       end: moment(value.checkout).format(\"YYYY-MM-DD\"),\n                       title: value.room.name,\n                       className: \"block_event\",\n                       extendedProps: {type:'block'}\n                   })\n                } else{\n                   if (value.otum.provider === \"rakuten\") {\n                     this.events.push({\n                         id: value.id,\n                         start: moment(value.checkin).format(\"YYYY-MM-DD\"),\n                         end: moment(value.checkout).format(\"YYYY-MM-DD\"),\n                         title: value.room.name,\n                         className: \"rakuten_event\",\n                         extendedProps: {type:'rakuten'}\n                     })\n                   } else if (value.otum.provider === \"booking\") {\n                     this.events.push({\n                         id: value.id,\n                         start: moment(value.checkin).format(\"YYYY-MM-DD\"),\n                         end: moment(value.checkout).format(\"YYYY-MM-DD\"),\n                         title: value.room.name,\n                         className: \"booking_event\",\n                         extendedProps: {type:'bookign'}\n                     })\n                   }\n                }\n            })\n          })\n          .catch(error => {\n            console.log('error')\n            // this.alert = { type: error.data, message: error.data.message }\n          })\n        },\n        eventClick(info) {\n            this.mode = 'detail'\n            this.detail = this.bookings.find((item, index, array) => {\n                    return item.id == info.event.id\n            })\n            return (info.event.extendedProps.type !== 'block') ?\n            this.toggleModal() : false\n        },\n        dateClick(info) {\n            this.mode = 'stock'\n            this.toggleModal()\n        }\n    },\n    created () {\n        this.init()\n    }\n}\n</script>\n<style style=\"scss\">\n.rakuten_event {\n    background-color: #f44336;\n    font-size: 18px;\n    /*height: 100vh;*/\n    text-align: center;\n    border: none;\n}\n.booking_event {\n    background-color: #f44336;\n    font-size: 18px;\n    /*height: 100vh;*/\n    text-align: center;\n    border: none;\n}\n.block_event {\n    background-color: #CCC;\n    font-size: 18px;\n    /*height: 100vh;*/\n    text-align: center;\n    border: none;\n}\n\n/*calender*/\n.fc-timeGrid-view .fc-day-grid .fc-row {\n  height: 100vh;\n}\n.fc-time-grid-container, .fc-time-grid {\n  display: none;\n}\n</style>"]}]);

